@import url("https://fonts.googleapis.com/css2?family=Bree+Serif&family=PT+Serif:ital,wght@0,400;0,700;1,400;1,700&display=swap");

*
{
  margin: 0;
  padding: 0;
}
body
{
 background: url("https://th.bing.com/th/id/R.78b0bfde77bb81a0a1acb397302d494c?rik=BaI%2f%2bC0iBLCO9g&riu=http%3a%2f%2fwallup.net%2fwp-content%2fuploads%2f2016%2f05%2f25%2f251398-pattern.jpg&ehk=FNK%2b3bizJsSs1TNfnDaWCDrymJ%2bZZx5iIuPhLvY7gm4%3d&risl=&pid=ImgRaw&r=0");
font-family: "Bree Serif", serif;
display: flex;
justify-content: center;
align-items: center;
min-height: 100vh;
}
.header
{
  text-align: center;
  color: rgb(59, 45, 20);
  border-bottom: 20px;
}
.app
{
  background: linear-gradient(90deg,#00ffd2 0%, #cb5bc9 80%);
  width: 95%;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  display: flex;
  justify-content: space-evenly;
}
.score-section
{
  display: flex;
  align-items: center;
  font-size: 20px;
  color: white;
  font-weight: bold;
}
.question-section
{
  width: 100%;
  position: relative;
}
.question-count
{
  margin-bottom: 20px;
  font-size: 20px;
}
.question-count span
{
  font-size: 30px;
}
.question-text
{
  color: aliceblue;
  font-size: 30px;
  font-weight: bold;
}
.answer-section
{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}
button{
  width: 100%;
  font-size: 16px;
  color: blanchedalmond;
  background: linear-gradient(90deg,#00ffd2 0%, #cb5bc9 80%);
  border-radius: 15px;
  display: flex;
  padding: 8px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid blue;
  cursor: pointer;
}
